import React from 'react'
import { Button, Dropdown, Space } from 'antd';
import { DownOutlined } from '@ant-design/icons';

function DropDown() {

  const items = [
    {
      key: '1',
      label: (
        <div></div>
      )
    },
    {
      key: '2',
      label: (
        <div></div>
      )
    },
    {
      key: '3',
      label: (
        <div></div>
      )
    }
  ]

  return (
    <Dropdown
      menu={{
        items,
      }}
      trigger={['click']}
    >
     <Button>
      <Space>
        
        <DownOutlined/>
      </Space>
     </Button>
    </Dropdown>
  )
}

export default DropDown